import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const ExpansionPanel = withStyles({
  root: {
    border: 'none',
    boxShadow: 'none',
    '&:not(:last-child)': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
  },
  expanded: {
    margin: 'auto',
  },
})(MuiExpansionPanel);

const ExpansionPanelSummary = withStyles({
  root: {
    marginBottom: -1,
    minHeight: 56,
    '&$expanded': {
      minHeight: 56,
    },
  },
  content: {
    '&$expanded': {
    },
  },
  expanded: {},
})(props => <MuiExpansionPanelSummary {...props} />);

ExpansionPanelSummary.muiName = 'ExpansionPanelSummary';

const ExpansionPanelDetails = withStyles(theme => ({
  root: {
    padding: theme.spacing.unit * 2,
  },
}))(MuiExpansionPanelDetails);


class Header extends Component {
   state = {
    expanded: '',
  };

  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };


    render() {
        const { expanded } = this.state;

        return (
            <div>
                <div className="header">
                	<picture>
                		<source media="(max-width: 480px)" srcSet={process.env.PUBLIC_URL + "/img/logo-p.png"}/>
                		<img src={process.env.PUBLIC_URL + "/img/logo-g.png"} alt="Logomarca"/>
                	</picture>
                </div>

                  <ExpansionPanel style={{backgroundColor:"#E0E0E0"}} expanded={expanded === 'panel1'} onChange={this.handleChange('panel1')}>
                      <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Typography variant="title" color="inherit">Aqui o seu sonho ganha força</Typography>
                      </ExpansionPanelSummary>
                      <ExpansionPanelDetails>
                        <Typography variant="body2">
                          Independente se ele é considerado maluco, diferente, inovador, ou um negocio tradicional, que vai ser seu foguete para o sucesso, acreditamos nele e queremos te ajudar a chegar la...
                        </Typography>
                      </ExpansionPanelDetails>
                  </ExpansionPanel>

            </div>
        );
    }
}

export default Header;
